.select {
  display: grid;
  grid-column-gap: var(--ui-element-indent-medium);
  grid-row-gap: var(--ui-element-indent-medium);

  &__item {
    cursor: pointer;
    text-align: left;
    outline: none;
    margin: 0;
    -webkit-tap-highlight-color: transparent;
    user-select: none;

    font-size: var(--ui-font-size-md);
    font-weight: var(--ui-font-weight-bold);
    color: var(--ui-color-text-primary);
    background-color: var(--ui-color-background-primary);
    border: var(--ui-border-width-small) solid var(--ui-color-border-primary);

    &_size {
      &_small {
        padding: 4px 8px;
        border-radius: var(--ui-element-border-radius-small);
        height: var(--ui-element-size-small);
      }
  
      &_medium {
        padding: 8px 12px;
        border-radius: var(--ui-element-border-radius-medium);
        height: var(--ui-element-size-medium);
      }
  
      &_large {
        font-size: var(--ui-font-size-lg);
        padding: 12px 16px;
        border-radius: var(--ui-element-border-radius-large);
        height: var(--ui-element-size-large);
      }
    }

    &_selected {
      border-color: var(--ui-color-primary);
      color: var(--ui-color-primary-inverted);
      background-color: var(--ui-color-primary);
    }

    &:not(:disabled):not(&_selected):hover {
      border-color: var(--ui-color-primary-lightened);
    }

    &:not(:disabled):not(&_selected):active {
      border-color: var(--ui-color-primary-darkened);
    }

    &:disabled {
      cursor: not-allowed;
      color: var(--ui-color-text-disabled);
      background-color: var(--ui-color-background-disabled);
      border-color: var(--ui-color-border-primary);
    }

    &:focus-visible {
      outline: var(--ui-border-width-medium) solid var(--ui-color-primary);
      outline-offset: 2px;
    }
  }
}